.gallery {
  position: relative;
  z-index: 100;
  margin: 7% 0;
}

.gallery__title {
  font-family: 'PlayfairDisplay', sans-serif;
  font-size: 24px;
  font-weight: 700;
  color: var(--color-accent);
  text-align: center;
  margin-bottom: 40px;
}

.gallery__body {
  position: relative;
}

@media (any-hover: none) {
  .gallery__body {
    overflow: auto;
  }
}

.gallery__items {
  display: grid;
  grid-template-columns: auto;
  grid-auto-flow: column;
  align-items: center;
}

@media (any-hover: hover) {
  .gallery__items {
    justify-content: center;
  }
}

.gallery__column {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.gallery__column:first-child {
  justify-content: flex-end;
}

.gallery__row {
  display: inline-grid;
  padding: 0 8px;
  grid-gap: 20px;
  grid-template-columns: auto;
  grid-auto-flow: column;
}

.gallery__row:not(:last-child) {
  margin: 0 0 16px 0;
}

.row-gallery_left-top {
  align-items: end;
  justify-content: end;
}

.row-gallery_left-bottom {
  align-items: start;
  justify-content: end;
}

.row-gallery_center {
  display: block;
}

.row-gallery_right-top {
  align-items: end;
}

.row-gallery_right-bottom {
  align-items: start;
}

.row-gallery__item img {
  border-radius: 5px;
  transition: all 0.3s ease;
}

.row-gallery__item:hover img {
  transform: scale(1.1);
}
